export default {
  name: 'VelocityGroup',
  methods: {
    enter (el, complete) {
      this.$emit('enter', { el, complete })
    },
    leave (el, complete) {
      this.$emit('leave', { el, complete })
    },
    afterLeave () {
      this.$emit('afterLeave')
    }
  }
}



