export default {
  name: 'support-box',

  props: {
    shake: {
      type: Boolean,
      required: false,
      default: false
    }
  },

  watch: {
    shake(val) {
      if (val === true) {
        this.$el.classList.add('animate-shake');
        setTimeout(() => {
          this.$el.classList.remove('animate-shake');
          if (typeof this.$parent.shake !== 'undefined') {
            this.$parent.shake = false;
          }
        }, 1000)
      }
    }
  }
}



