import { required } from 'vuelidate/lib/validators';

import flash from '../mixins/flashMixin';
import handleErrors from '../mixins/handleErrorsMixin';

import FlashMessage from '../components/FlashMessage.vue';
import FormRow from '../components/FormRow.vue';
import SupportBox from '../components/SupportBox.vue';
import VueButton from '../components/VueButton.vue';
import VueForm from '../components/VueForm.vue';
import VueInput from '../components/VueInput.vue';
import VueLabel from '../components/VueLabel.vue';

export default {
  name: 'login',

  mixins: [flash, handleErrors],

  data() {
    return {
      loading: false,
      shake: false,
      login: '',
      password: ''
    }
  },

  validations: {
    login: {
      required
    },
    password: {
      required
    }
  },

  components: {
    FlashMessage,
    FormRow,
    SupportBox,
    VueButton,
    VueForm,
    VueInput,
    VueLabel
  },

  methods: {
    validateBeforeSubmit() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        this.shake = true;
      } else {
        this.loading = true;
        this.submit().finally(() => {
          this.loading = false;
        });
      }
    },

    submit() {
      const credentials = {
        login: this.login,
        password: this.password
      };

      this.password = '';

      return this.$store.dispatch('LOGIN',
        credentials
      ).then((data) => {
        const redirect = this.$store.getters.redirect;
        this.$store.dispatch('FORGET_LOGIN_REDIRECT');
        this.$router.push(redirect || '/');
      }).catch((data) => {
        if (data.status && data.status === 500) {
          this.showFlashMessage('Authentication failed.', 'error');
        } else {
          this.showFlashMessage(data.message, 'error');
        }
        this.$nextTick(() => {
          this.$refs.password.focus();
        });
      });
    }
  }
}



