import { mapGetters } from 'vuex';
import dropdown from '../mixins/dropdownMixin';

import UserMenu from './UserMenu.vue';

export default {
  name: 'user-menu',

  mixins: [dropdown],

  components: {
    UserMenu
  },

  computed: {
    ...mapGetters([
      'user',
      'isAuthenticated'
    ]),

    username() {
      return this.user.username;
    },

    containerClasses() {
      return {
        'user-menu__open': this.dropdownOpen
      }
    }
  }
}



