export default {
  name: 'vue-button',

  props: {
    loading: {
      type: Boolean,
      required: false,
      default: false
    },
    disabled: {
      type: Boolean,
      required: false,
      default: false
    }
  },

  computed: {
    buttonClasses() {
      return {
        'button': true,
        'button__loading': this.loading
      };
    },

    spinnerClasses() {
      return [
        'button--spinner'
      ]
    },
  },

  methods: {
    onClick(event) {
      this.$emit('click', event);
    }
  }
}



