import { mapGetters } from 'vuex';
import date from '../mixins/dateMixin';

import GameVote from './GameVote.vue';

export default {
  name: 'game',

  mixins: [date],

  props: {
    game: {
      type: Object,
      required: true
    }
  },

  components: {
    GameVote
  },

  data() {
    return {
      focused: false
    }
  },

  computed: {
    ...mapGetters([
      'teamById'
    ])
  },

  methods: {
    onFocus() {
      this.$emit('gameFocus', this.game.id);
    },

    onBlur() {
      this.$emit('gameBlur', this.game.id);
    }
  }
}



