export default {
  name: 'vue-input',

  props: {
    value: {
      type: [String, Number],
      default: ''
    },
    id: {
      type: String,
      required: true
    },
    model: {
      required: false
    },
    type: {
      type: String,
      required: true
    },
    placeholder: {
      type: String,
      required: false,
      default: ''
    },
    icon: {
      type: String,
      required: false
    },
    autofocus: {
      type: Boolean,
      required: false,
      default: false
    },
    error: {
      type: Boolean,
      required: false,
      default: false
    }
  },

  computed: {
    inputWrapperClasses() {
      return {
        'input-error': this.error
      }
    },

    inputClasses() {
      return [
        { 'has-icon': !!this.icon },
        { 'has-error': this.error }
      ]
    },
    iconClasses() {
      return [
        'input-icon',
        `icon-${this.icon}`
      ];
    }
  },

  methods: {
    onChange(value) {
      this.$emit('change', value);
    },

    onInput(value) {
      this.$emit('input', value);
    },

    onFocus(event) {
      this.$emit('focus', event);
    },

    onBlur(event) {
      this.$emit('blur', event);
    },

    onKeydown(event) {
      this.$emit('keydown', event);
    },

    focus() {
      this.$refs.input.focus();
    }
  },

  mounted() {
    if (this.autofocus) {
      this.focus();
    }
  }
}



