import { mapGetters } from 'vuex';

export default {
  name: 'playoffs',

  data() {
    return {
      medalIcon3rd: global.window.AppConfig.medalIcon3rd,
      trophyIcon: global.window.AppConfig.trophyIcon
    }
  },

  computed: {
    ...mapGetters([
      'periodLastSixteen',
      'periodQuarterFinal',
      'periodSemiFinal',
      'period3rdPlace',
      'periodFinal',
    ])
  }
}



