import { mapGetters } from 'vuex';
import { required, integer, minValue } from 'vuelidate/lib/validators';

import canVote from '../mixins/canVoteMixin';

import VueLabel from './VueLabel.vue';
import VueInput from './VueInput.vue';
import VueButton from './VueButton.vue';

export default {
  name: 'game-vote',

  mixins: [canVote],

  props: {
    game: {
      type: Object,
      required: true
    }
  },

  components: {
    VueButton,
    VueInput,
    VueLabel
  },

  data() {
    return {
      loading: false,
      initialResult1: '',
      initialResult2: '',
      result1: '',
      result2: '',
      voted: false
    }
  },

  validations() {
    return {
      result1: {
        required,
        integer,
        minValue: minValue(0)
      },
      result2: {
        required,
        integer,
        minValue: minValue(0)
      }
    }
  },

  computed: {
    ...mapGetters([
      'userTipByGameId',
      'token'
    ]),

    hasChanged() {
      return (
        parseInt(this.initialResult1) !== parseInt(this.result1) ||
        parseInt(this.initialResult2) !== parseInt(this.result2)
      );
    }
  },

  methods: {
    onBlur(event) {
      this.$emit('voteBlur', event);
    },

    onFocus(event) {
      this.$emit('voteFocus', event);
    },

    onKeydown (event) {
      if (
        // Allow: backspace, delete, tab, escape, enter
        [8, 46, 9, 27, 13].indexOf(event.keyCode) !== -1 ||
        // Allow: Ctrl+A
        (event.keyCode === 65 && event.ctrlKey === true) ||
        // Allow: Ctrl+C
        (event.keyCode === 67 && event.ctrlKey === true) ||
        // Allow: Ctrl+V
        (event.keyCode === 86 && event.ctrlKey === true) ||
        // Allow: Ctrl+X
        (event.keyCode === 88 && event.ctrlKey === true) ||
        // Allow: home, end, left, right
        (event.keyCode >= 35 && event.keyCode <= 39)
      ) {
        return;
      }

      if (event.shiftKey ||
        (
          // Allow: num keys
          !(event.keyCode >= 48 && event.keyCode <= 57) &&
          // Allow: numpad keys
          !(event.keyCode >= 96 && event.keyCode <= 105)
        )
      ) {
        event.preventDefault();
      }
    },

    onSubmit(event) {
      this.loading = true;

      const vote = Object.assign({}, {
        game_id: this.game.id,
        result1: parseInt(this.result1),
        result2: parseInt(this.result2)
      });

      setTimeout(() => {
        this.$store.dispatch('VOTE', {
          token: this.token,
          vote: vote
        }).then((data) => {
          this.initialResult1 = vote.result1;
          this.initialResult2 = vote.result2;
          this.voted = true;

          if (data && data.message) {
            this.$notify({
              group: 'vote',
              title: 'Success!',
              text: data.message,
              type: 'success',
              duration: 6000
            });
          }
        }).catch((data) => {
          let errorMsg = '';

          if (data) {
            if (data.errors && data.errors.game_id && data.errors.game_id['is-voting-allowed']) {
              errorMsg = data.errors.game_id['is-voting-allowed'];
            } else if (data.message) {
              errorMsg = data.message;
            }
          }

          this.$notify({
            group: 'vote',
            title: 'Error!',
            text: errorMsg,
            type: 'error'
          });
        }).finally(() => {
          this.loading = false;
        });
      }, 200)
    }
  },

  mounted() {
    const userTip = this.userTipByGameId(this.game.id);
    if (userTip.voted) {
      this.initialResult1 = '' + userTip.result1;
      this.initialResult2 = '' + userTip.result2;
      this.result1 = '' + userTip.result1;
      this.result2 = '' + userTip.result2;
      this.voted = true;
    }
  }
}



