import { email, minLength, required, sameAs } from 'vuelidate/lib/validators';

import flash from '../mixins/flashMixin';
import handleErrors from '../mixins/handleErrorsMixin';

import FlashMessage from '../components/FlashMessage.vue';
import FormRow from '../components/FormRow.vue';
import SupportBox from '../components/SupportBox.vue';
import VueButton from '../components/VueButton.vue';
import VueForm from '../components/VueForm.vue';
import VueInput from '../components/VueInput.vue';
import VueInputError from '../components/VueInputError.vue';
import VueLabel from '../components/VueLabel.vue';

export default {
  name: 'signup',

  mixins: [flash, handleErrors],

  components: {
    FlashMessage,
    FormRow,
    SupportBox,
    VueButton,
    VueForm,
    VueInput,
    VueInputError,
    VueLabel
  },

  data() {
    return {
      loading: false,
      shake: false,
      email: '',
      username: '',
      password: '',
      passwordConfirmation: ''
    }
  },

  validations: {
    email: {
      required,
      email: email
    },
    username: {
      required,
      minLength: minLength(2)
    },
    password: {
      required,
      minLength: minLength(8)
    },
    passwordConfirmation: {
      required,
      sameAsPassword: sameAs('password')
    }
  },

  methods: {
    validateBeforeSubmit() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        this.shake = true;
        this.focusFirstFormControlWithError();
      } else {
        this.loading = true;
        this.submit().finally(() => {
          this.loading = false;
        });
      }
    },

    submit() {
      return this.$store.dispatch('SIGNUP', {
        email: this.email,
        username: this.username,
        password: this.password,
        password_confirmation: this.passwordConfirmation
      }).then(() => {
        // this.$router.push('/login');
      }).catch((data) => {
        this.showFlashMessage(data.message, 'error');
        this.setErrors(data.errors);
        this.focusFirstFormControlWithError();
      });
    }
  }
}



