import { mapGetters } from 'vuex';
import Vue from 'vue';

import canVote from '../mixins/canVoteMixin';

import Game from './Game.vue';

export default {
  name: 'upcoming-games',

  mixins: [canVote],

  components: {
    Game
  },

  data() {
    return {
      focused: {}
    }
  },

  computed: {
    ...mapGetters([
      'upcomingGames'
    ])
  },

  methods: {
    onFocus(gameId) {
      Vue.set(this.focused, gameId, true);
    },

    onBlur(gameId) {
      Vue.set(this.focused, gameId, false);
    }
  }
}



